import React, {useState, useEffect} from 'react'; 
import {projects} from './projects.js';
import Error from './Error.js';

const Profile = () => {

    let [list, setList] = useState(projects);
    let [data] = useState(projects);
    const [loading, setLoading] = useState(true);
    const [details, setDetails] = useState({name: '', secondName: '', email: '', tel: ''});
    const [showProfileSwitch, setShowProfileSwitch] = useState(false);

    const pathName = window.location.pathname;
    const urlInput = pathName.slice(1, pathName.length);
    console.log(urlInput);

let handleChange = (e) => {

      setList(data.filter(value=> {
          return value.languages_tag.toLowerCase().includes(e.target.value.toLowerCase())
      }))

  }

  const showProfile = () => {

    const {name, secondName, email, tel} = details;

    return (
        <div>
    <div id='pageContainer'>
     <div id='contentWrapper'>

    <div id='titleHolder'>
    <div id='projectPageTitle'>{name} {secondName} <br/> Project Page</div>
    <div id='projectPageTitle2'>email: {email} <br />tel: {tel}</div>
    </div>
    
    <br />

    <input type='text' id='searchBar' placeholder='please enter search term, e.g. react, js, jQuery' onChange={handleChange} />
    <i className="fas fa-search"></i>

    
    {/* start of the filtering code  */}


    <div id='container'>

{list.map((value, index)=> {
    let {name, img, url, description, languages} = value;
    return (
   

    <div className='holderBox' key={index}>
        <a href={url} target='_blank'><img src={img} alt=''></img>
        <hr />
        <h2>Name: {name}</h2>
        <p><strong>Languages used:</strong> {languages}</p>
        <p><strong>Descritpion:</strong> {description}</p>
        </a>
        </div>
        
    
               )
})}
</div>
    {/* end container div */}

    <div id='footerMargin'></div>
         

    <footer>
    <p></p>
    <p>email: {email}</p>
    <p>mobile: {tel}</p>
    </footer>


{/* end of Content Wrapper */}
    </div>


    {/* end of Page Wrapper */}
    </div>

        </div>
    )

}




useEffect(()=> {

    setLoading(false);
    let nameSplit = urlInput.split('-');
 

    const fName = nameSplit[0][0].toUpperCase() + nameSplit[0].slice(1, nameSplit[0].length);
    const sName = nameSplit[1][0].toUpperCase() + nameSplit[1].slice(1, nameSplit[1].length);
   

    if(urlInput === 'dmitri-sanzharov'){
        setShowProfileSwitch(true);
        setDetails({...details, 
            name: fName,
            secondName: sName,
            email: fName+sName+'@gmail.com',
            tel: '+353 870695207'
        })
    }

    if(urlInput === 'dmitri-white'){
        setShowProfileSwitch(true);
        setDetails({...details, 
            name: fName,
            secondName: sName,
            email: 'dimas15@gmail.com',
            tel: '+353 894915534'
        })
    }


}, [])


// useEffect(()=> {
//     let nameSplit = urlInput.split('-');
//     console.log(nameSplit);
// }, [])





if(loading){
    return (
        <div>
            <h1>Loading, please wait...</h1>
        </div>
    )
}



// HERE IS THE MAIN PROFILE SET UP

  return       ( <>
    {/* div soup */}

    {showProfileSwitch ? showProfile() : <Error />};

      {/* div soup */}
      </>
)
};

export default Profile;